* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}


img {
  width: 100%;
  height: 100%;
}

// ul,
// li {
//   margin: 0;
//   padding: 0;
//   list-style: none;
// }

a {
  text-decoration: none;
}

picture {
  margin: 0;
  padding: 0;
  border: 0;
}

svg {
  fill: currentColor;
  width: 100%;
}

.none {
  display: none;
}

.visualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.d-fl-ar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;

  @media(min-width: @width-tablet) {
    justify-content: space-between;
  }

}

.d-fl-bw {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

}


// Кнопка вверх
.to-top {
  position: fixed;
  bottom: 40px;
  right: 30px;
  width: 40px;
  height: 40px;
  z-index: 10;
  opacity: 0.6;

}

.to-top__icon {
  width: 100%;
  height: 100%;
  fill: rgba(255, 255, 255, 0.7);

  &:hover {
    cursor: pointer;
    fill: @color-light-brown;
  }
}