.header {
  position: relative;
  padding-top: 20px;
  color: @color-white;
  text-align: center;

  @media(min-width: @width-tablet) {
    padding-top: 39px;
  }

  &__container {
    background-color: transparent;
    z-index: 5;
  }

  

  // .header__subtitle

  &__subtitle {
    font-family: "Raleway";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.17;

    @media(min-width: @width-tablet) {
      font-size: 30px;
    }

    @media(min-width: @width-desktop) {
      font-size: 36px;
    }

  }

  // .header__title

  &__title {
    font-family: "Raleway";
    font-style: normal;
    font-weight: 600;
    font-size: 25px;
    line-height: 1;

    @media(min-width: @width-tablet) {
      font-size: 40px;
    }

    @media(min-width: @width-desktop) {
      font-size: 52px;
    }

  }

  // .header__text

  &__text {
    font-family: "montserrat";
    font-style: normal;
    font-weight: normal;

  }
}


